:root {
  --main-font-family: "Montserrat", sans-serif;
  --main-dark-color: #000;
  --main-light-color: #fff;
  --main-dark-text-color: #131313;
  --main-light-text-color: #fcfcfc;
  --background-color: #84a178;
  --current-color: #f8d24e;
  --hover-focus-color: #0a8817;
  --button-disabled-color: #ababab;
  --background-product-color1: #aec6a5;
  --background-product-color2: #efd478;
  --background-product-color3: #d6936d;
  --border-imput-color: #84a178;
  --background-imput-color: rgba(248, 210, 78, .1);
  --slider-color: #d9d9d9;
  --slider-active-color: #84a178;
  --main-color: #757575;
  --secondary-color: #212121;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --timing-duration: 250ms;
  --transition-color-link-func: color 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-background-color-func: background-color 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-box-shadow-func: box-shadow 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-border-color-funk: border-color 250ms cubic-bezier(.4, 0, .2, 1);
  --box-shadow-color: rgba(0, 0, 0, .15);
  --box-shadow-modal: 0px 1px 3px rgba(0, 0, 0, .12), 0px 1px 1px rgba(0, 0, 0, .14), 0px 2px 1px rgba(0, 0, 0, .2);
  --box-shadow-button: 0px 3px 1px rgba(0, 0, 0, .1), 0px 1px 2px rgba(0, 0, 0, .08), 0px 2px 2px rgba(0, 0, 0, .12);
  --transition-fill-color: fill 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-opaciti-visibiliti: opacity 250ms cubic-bezier(.4, 0, .2, 1), visibility 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.text-transform {
  text-transform: uppercase;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

